import * as R from 'ramda'
import { validate as validateSpec } from 'spected'
import { isNilOrEmpty, isNotNilOrEmpty } from './ramda-extra'

const renderMsg = (tmpl, arg) => (R.is(Function, tmpl) ? tmpl(arg) : tmpl)

const rules = {
  match: (pattern, msg = `should match: ${pattern}`) => [
    R.test(pattern),
    renderMsg(msg, pattern),
  ],

  minLength: (length, msg = `should have min length: ${length}`) => [
    R.pipe(R.length, R.gte(R.__, length)),
    renderMsg(msg, length),
  ],

  notBlank: (msg = 'should not be blank') => [isNotNilOrEmpty, msg],
}

const hasNoErrors = results => {
  const shouldExclude = R.cond([
    [isNilOrEmpty, R.T],
    [R.is(Object), hasNoErrors],
  ])
  return R.pipe(R.reject(shouldExclude), R.isEmpty)(results)
}

const defineValidator = ({ spec }) => {
  const validate = validateSpec(R.always(null), R.identity, spec)

  return {
    allValid: R.pipe(validate, hasNoErrors),
    checkField: (path, input) => R.pipe(validate, R.path(path))(input),
    checkAll: validate,
  }
}

export { defineValidator, rules }
